@use 'base/mixins' as m;
@use 'base/variables' as v;

.webp {
    .admin {
        @include m.bgOpaco('../img/FTel.webp', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.not-webp {
    .admin {
        @include m.bgOpaco('../img/FTel.webp', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.admin {
    min-height: 100vh;
    padding: 4rem 0;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
        @include m.tablet { 
            padding: 0;
            background-position: center top;
        }
    }
    .busqueda {
        &__heading {
            color: v.$blanco;
            margin-bottom: 2rem;
        }
    }
    .cita {
        &__heading {
            color: v.$azul;
            font-size: 3.5rem;
        }
        &__descripcion {
            text-align: center;
            font-weight: 700;
            font-size: 1.8rem;
            color: v.$blanco;
        }
    }
    .layoutb {
        margin-bottom: 2rem;
    }
    .formulario {
        input {
            background-color: v.$gris;
            color: v.$negro;
            border: none;
        }
    }
    .footer {
        &__titulo {
            color: v.$blanco;
        }
    }
}