@use 'base/mixins' as m;
@use 'base/variables' as v;

.webp {
    .citaseccion {
        background-color: v.$cafec;
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.not-webp {
    .citaseccion {
        background-color: v.$cafec;
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.jpg', v.$opaco);
        }
    }
}
.citaseccion {
    min-height: 100vh;
    padding: 4rem 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
    .formulario {
        label {
            color: v.$negro;
            @include m.tablet { 
                color: v.$blanco;
            }
        }
        input {
            background-color: v.$gris;
            border: none;
            color: v.$negro;
            &:disabled {
                background-color: v.$rojo;
                color: v.$negro;
            }
        }
    }
}
.cita {
    &__heading {
        font-family: v.$fprincipal;
        color: v.$azul;
        font-weight: 900;
        font-size: 3.5rem;
    }
    &__descripcion {
        text-align: center;
        color: v.$negro;
        font-weight: 700;
        font-family: v.$fparrafos;
        font-size: 1.8rem;
        @include m.tablet { 
            color: v.$blanco;
        }
    }
}
.seccion {
    padding-top: 2rem;
    display: none;
    &__titulo {
        text-align: center;
        font-size: 3.5rem;
        @include m.tablet { 
            color: v.$blanco;
        }
    }
    &__descripcion {
        text-align: center;
        color: v.$negro;
        font-weight: 700;
        font-family: v.$fparrafos;
        font-size: 1.8rem;
        @include m.tablet { 
            color: v.$blanco;
        }
    }
}
.mostrar {
    display: block;
}
.ocultar {
    display: none;
}
