@use 'base/mixins' as m;
@use 'base/variables' as v;

.webp {
    .correo {
        @include m.bgOpaco('../img/FTel.webp', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.not-webp {
    .correo {
        @include m.bgOpaco('../img/FTel.jpg', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.jpg', v.$opaco);
        }
    }
}

.correo {
    padding: 3rem 0;
    min-height: 100vh;
    background-size: cover;
    background-position: right top;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include m.tablet { 
        background-position: center bottom;
        padding: 0;
    }
    h1 {
        margin-bottom: 5rem;
    }
    .mensaje {
        &__texto {
            padding: 0;
            margin-bottom: 5rem;
        }
        &__heading {
            font-family: v.$fprincipal;
            color: v.$azul;
            font-weight: 900;
            font-size: 3.3rem;
        }
        &__descripcion {
            font-family: v.$fsecundaria;
            color: v.$blanco;
            text-align: center;
            font-weight: 600;
            font-size: 1.8rem;
        }
    }
    &__contenedor {
        background-color: rgba(0, 0, 0, .3);
        border: 2px solid v.$negro;
        padding: 1rem;
        @include m.tablet { 
            background-color: rgba(0, 0, 0, .6);
            width: 80%;
            padding: 3rem;
        }
        @include m.desktop { 
            width: 50%;
        }
    }
}