@use 'base/mixins' as m;
@use 'base/variables' as v;

.webp {
    .servicioslyt {
        background-color: v.$cafec;
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.not-webp {
    .servicioslyt {
        background-color: v.$cafec;
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.servicioslyt {
    min-height: 100vh;
    padding: 4rem 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
}