@use 'base/mixins' as m;
@use 'base/variables' as v;

.webp {
    .crearservicio {
        @include m.bgOpaco('../img/FTel.webp', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}
.not-webp {
    .crearservicio {
        @include m.bgOpaco('../img/FTel.webp', v.$opaco);
        @include m.tablet { 
            @include m.bgOpaco('../img/FDesk.webp', v.$opaco);
        }
    }
}

.crearservicio {
    min-height: 100vh;
    padding: 4rem 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
    .cita {
        &__descripcion {
            color: v.$blanco;
        }
    }
    .formulario {
        margin-bottom: 2rem;
    }
    .footer {
        &__titulo {
            color: v.$blanco;
        }
    }
}